.btn:hover, .btn:link, .btn:visited{
      transition: all 0.2s;
      position: relative;
      cursor: pointer;
}
.btn:hover{
      transform: translateY(-3px);
      box-shadow: 0 2px 7px 0.5px rgba(var(--color-dark-1), 0.8);
}
.btn:hover::after{
      transform: scaleX(1.4) scaleY(1.6);
      opacity: 0;
}
.btn::after{
      content: "";
      display: inline-block;
      height: 100%;
      width: 100%;
      border-radius: 10rem;
      position: absolute;
      top:0;
      left: 0;
      z-index: -1;
      transition: all 0.4s;
      font-size: 1.6rem;
}

.btn--animated{
      animation:moveInBottom .5s ease-out .75s;
      animation-fill-mode: backwards;

}

@keyframes moveInBottom{
      0%{
          opacity:0;
          transform: translateY(3rem);
      }
  
  
      100%{
          opacity:1;
          transform: translate(0);
      }
  }