nav {
      justify-content: space-between;
}

nav>a {
      font-size: 1.5rem;
      font-weight: 800;
}

nav>a>span {
      overflow: hidden;
      background: linear-gradient(to right,
                  rgba(var(--color-deep-blue-1), 1),
                  rgba(var(--color-deep-blue-1), 0.5) 50%,
                  var(--color-dark) 50%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-size: 200% 100%;
      background-position: 100%;
      transition: background-position 0.3s ease;
      text-decoration: none;
      text-shadow: 0 3px 1px rgba(var(--color-dark-1), 0.2)
}

nav>a>span:hover {
      background-position: 0 100%;
}

nav>a>i {
      padding-right: 0.5rem;
}

nav>ul li:not(:nth-child(1)) {
      margin-left: 1rem;
}

.nav__login {
      border: 1px solid var(--color-deep-blue);
      padding: 0.3rem 0.5rem;
      border-radius: 5px;
      box-shadow: 0 2px 7px 0.5px rgba(var(--color-dark-1), 0.4);
      transform: translateY(0px);
      transition: all 0.4s ease;
}
.nav__login:hover{
      background-color: #00bfff;
      box-shadow: 0 2px 7px 0.5px rgba(var(--color-dark-1), 0.8);
      transform: translateY(10px);
}

.after-icon {
      display: none;
}

.icon {
      color: red;
      cursor: pointer;
}

.container-icon:hover>* {
      display: flex;
      cursor: pointer;
}

.after-icon {
      flex-direction: column;
}