html {
      font-size: 14px;
      color: var(--color-dark);
}

body {
      background: linear-gradient(to bottom,
                  var(--color-deep-blue) 0%,
                  var(--color-deep-blue) 20%,
                  rgba(var(--color-deep-blue-1), 0.7) 40%,
                  rgba(var(--color-deep-blue-1), 0.6) 60%,
                  rgba(var(--color-deep-blue-1), 0.5) 80%,
                  white 100%);
      background-attachment: fixed;
      padding: 1rem;
}

.block {
      padding: 1rem;
      border: 1px solid gray;
      border-radius: 5px;

}

.flex-column {
      display: flex;
      flex-direction: column;
      align-items: center;
}

.flex-row {
      display: flex;
      align-items: center;
}

img,
.profile-icon {
      width: 2rem;
      height: 2rem;
      border-radius: 50%;
      box-shadow: 0 5px 28px rgba(var(--color-dark-1), 0.25),
            0 4px 10px rgba(var(--color-dark-1), .22);

}